
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

@font-face {
  font-family: 'wbficonregular';
  src: url(/static/fonts/wbficon.8e3ddd4.eot) format('embedded-opentype'), url(/static/img/wbficon.067c14a.svg) format('svg'), url(/static/fonts/wbficon.040bb38.woff) format('woff'), url(/static/fonts/wbficon.b74cdfe.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
.weibo-model {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.weibo-model * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.weibo-model a {
  text-decoration: none;
}
.weibo-model .weibo-background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 20180904;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0;
}
.weibo-model .weibo-main {
  background-color: #fff;
  max-width: 90%;
  min-height: 10px;
  border-radius: 3px;
  border-top: 2px solid #fa7f40;
  position: fixed;
  z-index: 20180905;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  /*transform: scale(0.1, 0.1);*/
  opacity: 0;
}
.weibo-model .weibo-main .weibo-title {
  width: 100%;
  border-bottom: 1px solid #f2f2f5;
  line-height: 38px;
  font-size: 14px;
  font-weight: 700;
  padding: 0 0 0 16px;
  vertical-align: middle;
  text-align: left;
  cursor: move;
}
.weibo-model .weibo-main .weibo-close {
  font-family: "wbficonregular" !important;
  font-size: 18px;
  position: absolute;
  z-index: 2;
  right: 12px;
  top: 0;
  color: #696e78;
}
.weibo-model .weibo-main .weibo-close:hover {
  color: #fa7f40;
}
.weibo-model .weibo-main .weibo-content {
  min-height: 60px;
  width: 100%;
  padding: 13px;
  text-align: left;
}
.weibo-model .weibo-main .weibo-btn {
  padding: 10px 13px;
  text-align: right;
}
.weibo-model .weibo-main .weibo-btn button {
  padding: 0 10px;
  margin-left: 8px;
  white-space: nowrap;
  display: inline-block;
  border-radius: 2px;
  height: 26px;
  line-height: 27px;
  text-decoration: none;
  font-size: 12px;
  min-width: 60px;
  text-align: center;
  outline: none;
  cursor: pointer;
}
.weibo-model .weibo-main .weibo-btn .ok {
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  background: #fa7f40;
  border: 1px solid #f06923;
  color: #fff;
}
.weibo-model .weibo-main .weibo-btn .ok:hover {
  background-color: #f7671d;
}
.weibo-model .weibo-main .weibo-btn .cancel {
  -webkit-box-shadow: 0 1px 2px #eee;
          box-shadow: 0 1px 2px #eee;
  background: #fff;
  border: 1px solid #eee;
  color: #696e78;
}
.weibo-model .weibo-main .weibo-btn .cancel:hover {
  background-color: #f9f9f9;
}
.weibo-model.display .weibo-background {
  opacity: 1;
}
.weibo-model.display .weibo-main {
  /*transform: scale(1, 1)*/
  opacity: 1;
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-name: bounceIn;
  animation-name: bounceIn;
}
@-webkit-keyframes bounceIn {
from,
  20%,
  40%,
  60%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
}
70% {
    opacity: 0.5;
    -webkit-transform: scale3d(1.1, 1.1, 1.1);
    transform: scale3d(1.1, 1.1, 1.1);
}
to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
}
}
@keyframes bounceIn {
from,
  20%,
  40%,
  60%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
}
70% {
    opacity: 0.5;
    -webkit-transform: scale3d(1.1, 1.1, 1.1);
    transform: scale3d(1.1, 1.1, 1.1);
}
to {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
}
}
.bounceIn {
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-name: bounceIn;
  animation-name: bounceIn;
}
